</template>

<script>
import {
  getblockslist,
  getdeallist,
  getblockApply,
  getonedaytrade,
  getwalletAll,
  getchain,
  getthirdtytrade,
  getuserinfo,
  searchwallet,
  searchblock,
  getmr,
} from "../../../api";
import moment from "moment/moment";
export default {
  mounted() {
    this.unit = sessionStorage.getItem("unit")
      ? sessionStorage.getItem("unit")
      : "MCN";
    this.enname = sessionStorage.getItem("en")
      ? sessionStorage.getItem("en")
      : "Morley";
    //获取最新区块列表25条
    getblockslist({ orderBy: "timestamp:desc", page: 1, limit: 25 })
      .then((res) => {
        for (let v of res.data.data) {
          v.timestamp.human = moment(v.timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          //   // if (v.transactions == 0) {
          //   //   v.transactions = "产出10U";
          //   // }
          //   v.h = v.height;
        }

        this.newsList = res.data.data;
      })
      .catch((err) => {
        window.console.log(err);
      });
    //获取最新交易25条
    getdeallist({ orderBy: "timestamp:desc", limit: 25 })
      .then((res) => {
        for (let v of res.data.data) {
          v.timestamp.human = moment(v.timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          if (v.type == 6) {
            for (let r of v.asset.payments) {
              v.amount = v.amount * 1 + r.amount * 1;
            }
          }
        }
        this.tradesList = res.data.data;
      })
      .catch((err) => {
        window.console.log(err);
      });
    // 获取区块高度供应量
    getblockApply()
      .then((res) => {
        this.blockheight = res.data.data.block.height;
        this.supply = res.data.data.supply;
      })
      .catch((err) => {
        console.log(err);
      });
    // 矿池销毁量
    var type = {
      type: 1,
    };
    if (this.unit == "MCN") {
      type.type = 1;
    }
    if (this.unit == "ACL") {
      type.type = 3;
    }
    getonedaytrade(type)
      .then((res) => {
        this.alldes = res.data.data.destroy;
      })
      .catch((err) => {
        console.log(err);
      });
    // 获取所有钱包地址

    getwalletAll()
      .then((res) => {
        this.alladdress = res.data.meta.totalCount;
      })
      .catch((err) => {
        console.log(err);
      });
    //获取侧链数量
    getchain()
      .then((res) => {
        this.chain = res.data.data.length;
      })
      .catch((err) => {
        console.log(err);
      });
    //获取近三十日交易
    var chaintype = {
      type: 1,
    };
    if (this.unit == "MCN") {
      chaintype.type = 1;
    } else if (this.unit == "MR") {
      chaintype.type = 2;
    } else {
      chaintype.type = 3;
    }
    getthirdtytrade(chaintype)
      .then((res) => {
        var data = [];
        var date = [];
        for (let v of res.data.data) {
          data.push(v.count);
          var n = v.date.split("-");

          date.push(`${n[1]}-${n[2]}`);
        }
        this.drawLine(data, date);
      })
      .catch((err) => {
        console.log(err);
      });

    //获取挖矿数据
    getuserinfo(type)
      .then((res) => {
        console.log(res);
        this.pledgeTotalAmount = res.data.data.pledgeTotalAmount;
        this.mineUserNum = res.data.data.mineUserNum;
        this.unlockTotalAmount =
          res.data.data.mineTotalAmount == null
            ? 0
            : res.data.data.mineTotalAmount;
      })
      .catch((err) => {
        console.log(err);
      });

    // 获取墨菲的三个数据
    getmr()
      .then((res) => {
        this.mrdes = (res.data.destroy_amount / 1000000000).toFixed(2);
        this.confirm_reward = (res.data.confirm_reward / 100000000).toFixed(2);
        this.fission_reward = (res.data.fission_reward / 100000000).toFixed(2);
      })
      .catch((err) => {
        console.log(err);
      });
    this.timer = setInterval(() => {
      //获取最新区块列表25条
      getblockslist({ orderBy: "timestamp:desc", page: 1, limit: 25 })
        .then((res) => {
          for (let v of res.data.data) {
            v.timestamp.human = moment(v.timestamp.human).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            //   // if (v.transactions == 0) {
            //   //   v.transactions = "产出10U";
            //   // }
            //   v.h = v.height;
          }

          this.newsList = res.data.data;
        })
        .catch((err) => {
          window.console.log(err);
        });
      //获取最新交易25条
      getdeallist({ orderBy: "timestamp:desc", limit: 25 })
        .then((res) => {
          for (let v of res.data.data) {
            v.timestamp.human = moment(v.timestamp.human).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            if (v.type == 6) {
              for (let r of v.asset.payments) {
                v.amount = v.amount * 1 + r.amount * 1;
              }
            }
          }
          this.tradesList = res.data.data;
        })
        .catch((err) => {
          window.console.log(err);
        });
      // 获取区块高度供应量
      getblockApply()
        .then((res) => {
          this.blockheight = res.data.data.block.height;
          this.supply = res.data.data.supply;
        })
        .catch((err) => {
          console.log(err);
        });
      // 矿池销毁量
      var type = {
        type: 1,
      };
      if (this.unit == "MCN") {
        type.type = 1;
      }
      if (this.unit == "ACL") {
        type.type = 3;
      }
      getonedaytrade(type)
        .then((res) => {
          // console.log(res)

          this.alldes = res.data.data.destroy;
        })
        .catch((err) => {
          console.log(err);
        });
      // 获取所有钱包地址

      getwalletAll()
        .then((res) => {
          this.alladdress = res.data.meta.totalCount;
        })
        .catch((err) => {
          console.log(err);
        });
      //获取侧链数量
      getchain()
        .then((res) => {
          this.chain = res.data.data.length;
        })
        .catch((err) => {
          console.log(err);
        });
      //获取近三十日交易
      var chaintype = {
        type: 1,
      };
      if (this.unit == "MCN") {
        chaintype.type = 1;
      } else if (this.unit == "MR") {
        chaintype.type = 2;
      } else {
        chaintype.type = 3;
      }
      getthirdtytrade(chaintype)
        .then((res) => {
          var data = [];
          var date = [];
          for (let v of res.data.data) {
            data.push(v.count);
            var n = v.date.split("-");
            date.push(`${n[1]}-${n[2]}`);
          }
          this.drawLine(data, date);
        })
        .catch((err) => {
          console.log(err);
        });

      //获取挖矿数据
      getuserinfo(type)
        .then((res) => {
          this.pledgeTotalAmount = res.data.data.pledgeTotalAmount;
          this.mineUserNum = res.data.data.mineUserNum;
        })
        .catch((err) => {
          console.log(err);
        });
      // 获取墨菲的三个数据
      getmr()
        .then((res) => {
          this.mrdes = (res.data.destroy_amount / 1000000000).toFixed(2);
          this.confirm_reward = (res.data.confirm_reward / 100000000).toFixed(
            2
          );
          this.fission_reward = (res.data.fission_reward / 100000000).toFixed(
            2
          );
        })
        .catch((err) => {
          console.log(err);
        });
    }, 10000);
  },
  methods: {
    drawLine(data, date) {
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      myChart.setOption({
        grid: {
          left: "1%",
          right: "1%",
          bottom: "1%",
          top: "8%",
          containLabel: true,
        },
        xAxis: {
          axisLine: {
            lineStyle: {
              color: "#FFF",
            },
          },
          type: "category",
          data: date,
          axisLabel: {
            inside: false,
            textStyle: {
              color: "rgba(0,0,0,0.3)",
            },
            interval: 6,
          },
        },
        yAxis: {
          axisLine: {
            lineStyle: {
              color: "#FFF",
            },
          },
          axisLabel: {
            interval: "2",
            formatter: function (value) {
              if (value >= 1000 && value < 10000000) {
                value = value / 1000 + "k";
              }
              return value;
            },
            textStyle: {
              color: "rgba(0,0,0,0.3)",
            },
          },
          splitLine: {
            show: false,
          },
          type: "value",
        },
        series: [
          {
            data: data,
            type: "line",
            smooth: true,
            symbol: "none",
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#000",
                },
              },
            },
          },
        ],
      });
    },
    searchInfo() {
      if (this.inpu !== "") {
        if (this.inpu.split("").length == 34) {
          window.scrollTo({
            top: 10,
            behavior: "smooth",
          });
          searchwallet({ address: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/walletdetails",
                query: {
                  id: res.data.data[0].address,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else if (this.inpu.split("").length == 64) {
          this.$router.push({
            path: "/tradedetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (this.inpu.split("").length == 6) {
          this.$router.push({
            path: "/blockdetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (
          this.inpu.split("").length == 20 ||
          this.inpu.split("").length == 19
        ) {
          searchblock({ id: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/blockdetails",
                query: {
                  id: res.data.data[0].height,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else {
          this.$notify.error({
            title: "警告",
            message: "请输入正确的钱包地址/交易id/区块高度",
            type: "warning",
            duration: 4500,
          });
          this.inpu = "";
        }
      }
    },
    handleSubmit2() {
      console.log(this.inpu.split("").length);
      if (this.inpu !== "") {
        if (this.inpu.split("").length == 34) {
          window.scrollTo({
            top: 10,
            behavior: "smooth",
          });
          searchwallet({ address: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/walletdetails",
                query: {
                  id: res.data.data[0].address,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else if (this.inpu.split("").length == 64) {
          this.$router.push({
            path: "/tradedetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (this.inpu.split("").length == 6) {
          this.$router.push({
            path: "/blockdetails",
            query: {
              id: this.inpu,
            },
          });
        } else if (
          this.inpu.split("").length == 20 ||
          this.inpu.split("").length == 19
        ) {
          searchblock({ id: this.inpu })
            .then((res) => {
              this.$router.push({
                path: "/blockdetails",
                query: {
                  id: res.data.data[0].height,
                },
              });
            })
            .catch((err) => {
              this.$alert("请输入正确钱包地址", "提示", {
                confirmButtonText: "确定",
              });
              this.inpu = "";
              window.console.log(err);
            });
        } else {
          this.$notify.error({
            title: "警告",
            message: "请输入正确的钱包地址/交易id/区块高度",
            type: "warning",
            duration: 4500,
          });
          this.inpu = "";
        }
      }
    },
    clicktable(p) {
      this.$router.push(p);
    },
    toblockdetails(e) {
      console.log(e);
      this.$router.push({
